import React, { useState, useCallback, useEffect } from "react";
import {
  Box,
  Button,
  Input,
  Typography,
  Card,
  CardContent,
  CardActions,
  Grid,
} from "@material-ui/core";
import { Link, useParams } from "react-router-dom";
import { Wrapper } from "../constants/Wrapper";
import StudentDetailsForm from "./StudentDetailsForm";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { fetchStudentDetails } from "../actions/homeworkActions";
import { getStudentDetails } from "../selectors/homeworkSelectors";
import capitalizeFirstLetter from "../utils/capitalizeFirstLetter";

function StudentDetails({ handleFetchStudentDetails, studentDetails }) {
  const [editing, setEditing] = useState(false);

  let { id } = useParams();

  const handleEditing = useCallback(() => {
    setEditing(true);
  }, []);

  useEffect(() => {
    if (id) {
      handleFetchStudentDetails(id);
    }
  }, [handleFetchStudentDetails, id]);

  return (
    <Wrapper>
      {editing ? (
        <StudentDetailsForm isEditing />
      ) : (
        <Box p={4}>
          <Link to="/homework">
            <Button color="primary">  Go back</Button>
          </Link>
          <Box display="flex" flexDirection="row" alignItems="center" ml={1}>
            <Box mr={2}>Student details</Box>
          </Box>

          <Box py={2} ml={1}>
            <Card>
              <Grid container>
                <Grid xs={6}>
                  <Box p={2}>
                    {" "}
                    First Name: {studentDetails?.firstName || "-"}
                  </Box>
                </Grid>
                <Grid xs={6}>
                  <Box p={2}>
                    {" "}
                    Last Name: {studentDetails?.lastName || "-"}{" "}
                  </Box>
                </Grid>
              </Grid>
              <Grid container>
                <Grid xs={6}>
                  <Box p={2}> Id: {studentDetails?.id || "-"}</Box>
                </Grid>
                <Grid xs={6}>
                  <Box p={2}> Email: {studentDetails?.email || "-"} </Box>
                </Grid>
              </Grid>{" "}
              <Grid container>
                <Grid xs={6}>
                  <Box p={2}>
                    {" "}
                    Gender:{" "}
                    {capitalizeFirstLetter(studentDetails?.gender || "-")}
                  </Box>
                </Grid>
                <Grid xs={6}>
                  <Box p={2}>
                    {" "}
                    Favourite Subjects:{" "}
                    {String(studentDetails?.favouriteSubjects || "-")}
                  </Box>
                </Grid>
              </Grid>
              <Grid container>
                <Grid xs={6}>
                  <Box p={2}>
                    {" "}
                    Total Time in books :{" "}
                    {studentDetails?.totalSpentInBooks || "-"}
                  </Box>
                </Grid>
                <Grid xs={6}>
                  <Box p={2}>
                    {" "}
                    Created at: {studentDetails?.created || "-"}{" "}
                  </Box>
                </Grid>
              </Grid>
              <Grid container>
                <Grid xs={6}>
                  <Box p={2} alignItems="flex-end">
                    <Button
                      variant="outlined"
                      color="primary"
                      onClick={handleEditing}
                    >
                      Edit
                    </Button>
                  </Box>
                </Grid>
              </Grid>
            </Card>
          </Box>
        </Box>
      )}
    </Wrapper>
  );
}

const mapStateToProps = (state) => ({
  studentDetails: getStudentDetails(state),
});

const mapDispatchToProps = {
  handleFetchStudentDetails: fetchStudentDetails,
};

StudentDetailsForm.propTypes = {
  isEditing: PropTypes.bool,
  studentDetails: PropTypes.object,
};

export default connect(mapStateToProps, mapDispatchToProps)(StudentDetails);
