import React from "react";
import { getNotification } from "../selectors/homeworkSelectors";
import { connect } from "react-redux";
import Alert from "@material-ui/lab/Alert";
import { Snackbar } from "@material-ui/core";
import { clearNotification } from "../actions/homeworkActions";

const NotificationProvider = ({ notification, children }) => {
  const handleClose = () => {
    clearNotification();
  };
  return (
    <div>
      {children}

      {/* <Snackbar open={notification.message} onClose={handleClose}>
        <Alert severity="success" sx={{ width: "100%" }}>
          {notification.message}
        </Alert>
      </Snackbar> */}
    </div>
  );
};

const mapStateToProps = (state) => ({
  notification: getNotification(state),
});

export default connect(mapStateToProps)(NotificationProvider);
