import React, { useEffect } from "react";
import { Box, Button } from "@material-ui/core";
import { connect } from "react-redux";
import {
  getFilteredStudents,
  getStudents,
} from "../selectors/homeworkSelectors";
import { fetchStudents } from "../actions/homeworkActions";
import StudentsTable from "./StudentsTable";
import { Wrapper } from "../constants/Wrapper";
import { Link } from "react-router-dom";

function Homework({ handleFetchStudents }) {
  useEffect(() => {
    handleFetchStudents();
  }, [handleFetchStudents]);

  return (
    <Wrapper>
      <Box p={4}>
        <Box display="flex" flexDirection="row" alignItems="center" ml={1}>
          <Box mr={2}>Students list</Box>{" "}
          <Link to="/add_student">
            <Button variant="contained" color="primary">
              Add student
            </Button>
          </Link>
        </Box>

        <Box py={2} ml={1}>
          <StudentsTable />
        </Box>
      </Box>
    </Wrapper>
  );
}

const mapStateToProps = (state) => ({
  students: getStudents(state),
  filteredStudents: getFilteredStudents(state),
});

const mapDispatchToProps = {
  handleFetchStudents: fetchStudents,
};

export default connect(mapStateToProps, mapDispatchToProps)(Homework);
